
<template>
  <div id="app">
    <!-- <img src="./assets/logo.png">
    <router-view/> -->
    <button @click="goHistoricalNews">跳转公共号历史消息</button>
    <button @click="goXcx">跳转小程序</button><br/>
    <p>说明:</p>
    <p>跳转公共号历史消息，只需要提供历史消息的URL即可</p><br/><br/>
    <p>跳转小程序暂时没有直接跳转的功能，但是可以绕一下，步骤如下</p>
    <p>跳转到微信文章，在由用户点击进入小程序</p>
    <p>需要在微信端发布一条带有小程序的推送文档，并提供这条信息的链接即可</p>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return{
      historicalNewsUrl:"https://mp.weixin.qq.com/mp/profile_ext?action=home&__biz=MzA4ODE5OTEzNA==&scene=124#wechat_redirect",
      xcxUrl:"https://mp.weixin.qq.com/s/gJysj78MGcWfYfmdjnQ1JQ"
    }
  },
  methods:{
    goHistoricalNews:function(){
      location.href=this.historicalNewsUrl
    },
    goXcx:function(){
      location.href=this.xcxUrl
    }
  }
}
</script>

<style>
p{
  text-align: left;
}
button{
  width: 200px;
  height: 40px;
  text-align: center;
  margin: auto;
  margin-bottom: 20px;
  
  display: block;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



